
body {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* text-align: center; */
  color: #2c3e50;
  width: 1200px;
  background-color: rgb(230, 230, 230);
  margin-left: auto;
  margin-right: auto;
}

/*メインコンテント*/
div#main_content {
  margin-top: 20px;
  padding-top: 20px;
  padding-left: 20px;
  padding-right: 20px;
  background-color:rgb(250, 250, 250)
}
h1#content-h1 {
  padding: 0.4em 0.4em;
  margin-left:-20px;
  margin-right: -20px;
  color: #494949;
  background: #f4f4f4;
  border-left: solid 10px #7db4e6;
  border-bottom: solid 3px #d7d7d7;
}

div#header {
  width: 1200px;
  background-color: violet;
  margin-left: auto;
  margin-right: auto;
  background: linear-gradient(400deg, #e4a972, #9941d8);
}
div#site_title:hover{
  cursor: pointer;
}
/*サイトのタイトル*/
div#site_title {
  font-size: 30px;
  padding-left: 10px;
  width: 300px;
  margin-right: auto;
}
/*お問い合わせ*/
div#contact {
  font-size: 14px;
  width: 100px;
  margin-left: auto;
  position: relative;
  bottom: 40px;
}
div#contact:hover{
  cursor: pointer;
}
/*ナヴィ*/
div#global_navi {
  height: 30px;
  background-color: rosybrown;
}
ul.navi-ul {
  text-align: center;
  font-size: 18px;
}
li a {
  display: block;
  width: 100%;
  text-decoration: none;
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease;
  background-color: rgb(82, 82, 218);
  color:white;
}
li a:hover{
  background-color: white;
  color: rgb(82, 82, 218);
}
li.tab {
  margin: 0px;
  margin-top: 4px;
  width: calc(960px / 7);
  display: inline-block;
  background-color: yellow;
}

div#headlines{
  padding-bottom:25px;
}

/*タイトルについて*/
h1#title{
  margin-top: 0px;
  display: inline-block;
}
h1#title{
  -webkit-transition: all 0.19s ease;
  transition: all 0.19s ease;
}
h1#title:hover{
  border-bottom: 3px solid black;
  cursor: pointer;
}

/* h1#title.fade-enter,h1#title.fade-leave-to{
  opacity: 0;
} */

/*見出しについて*/
div#headline {
  margin-bottom:10px;
  padding: 15px;
  border: 10px solid rgb(128, 116, 116);
  /* border-radius: 15px; */
}
img#headline-img {
  width: 300px;
  float:left;
  padding-right: 1em;
}
div#clear{
  clear: both;
}

div#single{
  padding-bottom:100px;
  min-height: 500px;
}
a#image{
  float: left;
}
div#single img {
  width: 500px;
  /* float:left; */
  padding-right:40px;
  padding-bottom:40px;
}
div#single p{
  line-height: 175%;
}

color.fade-enter-active, color.fade-leave-active {
  -webkit-transition: opacity 0.5s;
  transition: opacity 0.5s;
}
color.fade-enter, color.fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}
.color{
  color: red;
}
p#text_length {
  -webkit-transition: all 0.25s ease;
  transition: all 0.25s ease;
}
div#contact_page {
  font-size: 16px;
}
textarea {
  font-family: inherit;
  font-size: 100%;
}
p {
  margin-left: 30%;
  display: inline-block;
}
input,textarea {
  margin-right: 20%;
  margin-left: 2%;
  width: 400px;
}
textarea#input_id {
  margin-left: calc(2% + 80px);
}
div#center {
  text-align: center;
}
div#center p {
  margin-left: 40%;
  margin-top: 0px;
  display: block;
}
div#center button {
  margin-top: 50px;
  margin-bottom: 100px;
}

